<template>
  <div>
    <div v-show="checkPaymanage&&checkSerchmanage&&checkModifemanage">
      <div class="search_container">
        <el-form :inline="true" :model="params" class="demo-form-inline" ref="filterFormRef">
          <el-form-item label="业务日期" size="mini">
            <el-date-picker v-model="timeValue" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="单据号" size="mini">
            <el-input v-model="params.payment_sn" maxlength="100" placeholder="请输入" clearable @keyup.enter.native="this.onSearch(1)"></el-input>
          </el-form-item>
          <el-form-item label="财务组织" size="mini">
            <el-select clearable v-model="params.company_id" placeholder="请选择" @change="companyChange" @focus="companyListInvok" filterable maxlength="100">
              <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
                <span :class="item.type==1 ? 'optionStyle': ''">{{ item.company_name }}</span>
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="部门" size="mini">
            <el-select clearable v-model="params.department_id" @change="departmentChange" @focus="DepartmentListInvok" placeholder="请选择" filterable maxlength="100">
              <el-option :label="item.department_name" :value="item.department_id" :key="item.department_id" v-for="item in departmentList"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="供应商(厂家)" size="mini">
            <el-select clearable v-model="params.supplier_id" @change="supplierChangesave" maxlength="100" :filter-method="supplierListInvok" @focus="supplierListInvok" filterable placeholder="请选择">
              <el-option v-for="item in supplierList" :label="item.supplier_name" :value="item.supplier_id" :key="item.supplier_id">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="往来类型" size="mini">
            <el-select clearable v-model="params.factory_accounts_type_id" filterable maxlength="100" @change="factoryAccounts" @focus="getFactoryAccountInvok">
              <el-option v-for="item in factoryAccountsList" :label="item.factory_accounts_type_name" :value="item.factory_accounts_type_id" :key="item.factory_accounts_type_id">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="审核状态" size="mini">
            <el-select clearable v-model="params.auditor_status">
              <el-option label="全部" :value="0">全部</el-option>
              <el-option label="未审核" :value="1">未审核</el-option>
              <el-option label="已审核" :value="2">已审核</el-option>
              <el-option label="作废" :value="3">作废</el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="审核日期" size="mini">
            <el-date-picker v-model="timeValue1" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期" clearable>
            </el-date-picker>
          </el-form-item>
          <el-form-item size="mini" class="search-style search">
            <el-button @click="onSearch(1)" size="mini" type="primary">查询</el-button>
            <el-button plain @click="onReset" size="mini">重置</el-button>
          </el-form-item>
        </el-form>
        <div class="add_delet">
          <el-button icon="el-icon-circle-plus-outline" type="primary" class="addBtn" size="mini" @click="addFun" v-if="authority.b_add==1">新增</el-button>
          <el-button plain icon="el-icon-delete" size="mini" @click="delFun" v-if="authority.b_del==1">删除</el-button>
          <el-button class="iconfont icon-pass" type="primary" size="mini" @click="examineFun" v-if="authority.b_pass==1">审核</el-button>
          <el-button plain icon="iconfont icon-fanshenhe" @click="AUDIT_GET" size="mini" v-if="authority.b_nopass==1">弃审</el-button>
          <el-button plain class="iconfont icon-zuofei" size="mini" @click="abolishFun" v-if="authority.b_invalid==1&&!is_company_service">作废</el-button>
        </div>
      </div>
      <div class="table_container">
        <xTable :ajax='{ url: "getPaymentList", params: params }' :row-style='selectRowChangeBg' @cellDblclick='handleClick' ref='xTable' :footerMethod='footerMethod' :tableColumn='tableColumn' />
      </div>
    </div>

  </div>
</template>

<script>
import { tableHeader } from "./config/paymanage_default";
// import { mapActions } from "vuex";
export default {
  name: "purchasePaymanageDefault",
  mixins: [tableHeader],
  data() {
    return {
      authority: {},
      timeValue: [],
      timeValue1: [],
      checkPaymanage: true,
      checkSerchmanage: true,
      formInlineParams: {},
      manageInfo: {},
      checkModifemanage: true,
    };
  },
  methods: {
    // ...mapActions(["paymanageAdd_show", "paymanageSearch_show"]),
    factoryAccounts(val) {
      if (val) {
        this.params.factory_accounts_type_name = this.factoryAccountsList.filter(
          (obj) => obj.factory_accounts_type_id === val
        )[0].factory_accounts_type_name;
      }
    },
    supplierChangesave(val) {
      if (val) {
        this.params.supplier_name = this.supplierList.filter(
          (obj) => obj.supplier_id === val
        )[0].supplier_name;
      }
    },
    departmentChange(val) {
      if (val) {
        this.params.department_name = this.departmentList.filter(
          (obj) => obj.department_id === val
        )[0].department_name;
      }
    },
    selectRowChangeBg({ row }) {
      if (
        this.multipleSelection.length &&
        this.multipleSelection
          .map((obj) => obj.payment_id)
          .includes(row.payment_id)
      ) {
        return "background:#d4e1ee";
      } else {
        return "";
      }
    },
    AUDIT_GET() {
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: "请选择需要弃审的信息",
          type: "warning",
        });
        return false;
      }
      var addr = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].payment_id);
      }
      var rebateArr = addr.toString();
      this.$axMethod
        .auditorNoPassF({
          payment_id: rebateArr,
        })
        .then((res) => {
          if (res.data.code == 200) {
            this.$message({
              message: res.data.msg ? res.data.msg : res.data.data,
              type: "success",
            });
          }
          this.onSearch();
        });
    },
    companyChange(val) {
      if (val != "") {
        this.params.company_name = this.companyList.filter(
          (obj) => obj.company_id === val
        )[0].company_name;
      }
      this.$set(this.params, "supplier_id", "");
      this.$set(this.params, "department_id", "");
    },
    onSearch(num) {
      if (this.timeValue) {
        this.params.start_time = this.timeValue[0];
        this.params.end_time = this.timeValue[1];
      } else {
        this.params.start_time = this.params.end_time = "";
      }
      if (this.timeValue1) {
        this.params.start_auditor_time = this.timeValue1[0];
        this.params.end_auditor_time = this.timeValue1[1];
      } else {
        this.params.start_auditor_time = this.params.end_auditor_time = "";
      }
      this.footerData = {};
      this.$refs.xTable._getData(num);
      this.$axMethod.getPaymentListTotal(this.params).then(({ data }) => {
        if (data.code === 200) {
          this.footerData = data.data;
          this.$refs.xTable._proxy("updateFooter");
        }
      });
    },
    examineFun() {
      //审核
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: "请选择需要审核的信息",
          type: "warning",
        });
        return false;
      }
      var addr = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].payment_id);
      }
      var rebateArr = addr.toString();
      var funcan = {
        examineID: "payment_id",
        eachData: {
          payment_id: rebateArr,
        },
        examineApi: "paymentAuditorPass",
        list: {
          UrlApi: "getPaymentList",
          data: this.TableData,
          esData: {
            page: this.goPage,
          },
        },
      };
      this.$common.examineFun(funcan, this.onSearch);
    },
    abolishFun() {
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: "请选择需要作废的信息",
          type: "warning",
        });
        return false;
      }
      var addr = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].payment_id);
      }
      var rebateArr = addr.toString();
      var funcan = {
        examineID: "payment_id",
        eachData: {
          payment_id: rebateArr,
        },
        examineApi: "paymentAuditorInvalid",
        list: {
          UrlApi: "getPaymentList",
          data: this.TableData,
          esData: {
            page: this.goPage,
          },
        },
      };
      this.$common.examineFun(funcan, this.onSearch);
    },
    fexamineFun() {
      //弃审
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: "请选择需要审核的信息",
          type: "warning",
        });
        return false;
      }
      var addr = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].payment_id);
      }
      var rebateArr = addr.toString();
      var funcan = {
        examineID: "payment_id",
        eachData: {
          payment_id: rebateArr,
        },
        examineApi: "paymentAuditorNoPass",
        list: {
          UrlApi: "getPaymentList",
          data: this.TableData,
          esData: {
            page: this.goPage,
          },
        },
      };
      this.$common.examineFun(funcan, this.onSearch);
    },
    onReset() {
      this.$set(this, "params", {});
      this.timeValue = [];
      this.timeValue1 = [];
      this.onSearch(1);
    },
    addFun() {
      this.$router.push({ path: "/purchase/paymanage/paymangeAdd" });
      //增加方法
      // this.paymanageAdd_show({
      //   // commont: "paymanageAdd",
      //   addORchange: "add",
      // });
    },
    delFun() {
      //删除方法
      var deledata = {
        deleID: "payment_id",
        deleSelect: this.multipleSelection,
        deleApi: "delPayment",
        list: {
          UrlApi: "getPaymentList",
          data: this.TableData,
          esData: {
            page: this.goPage,
          },
        },
      };
      this.$common.deletes(deledata, "删除", this, this.onSearch);
    },
    handleClick({ row }) {
      this.$router.push({
        path: "/purchase/paymanage/paymanageSearch",
        query: {
          payment_id: row.payment_id,
        },
      });
      // this.paymanageSearch_show({
      //   // commont: "paymanageSearch",
      //   payment_id: row.payment_id,
      //   params: this.params,
      //   goPage: this.goPage,
      //   pageSize: this.pageSize,
      // });
    },
    updateClick(row) {
      this.$router.push({
        path: "/purchase/paymanage/paymangeAdd",
        query: {
          payment_id: row.payment_id,
        },
      });
      // this.paymanageAdd_show({
      //   // commont: "paymanageAdd",
      //   payment_id: row.payment_id,
      //   addORchange: "change",
      //   params: this.params,
      //   goPage: this.goPage,
      //   pageSize: this.pageSize,
      // });
    },
  },
  activated() {
    this.onSearch(window.goPage);
  },
};
</script>
<style scoped>
</style>
