export const tableHeader = {// 需要传进来一个this对象
  data() {
    return {
      footerData: {},
      tableColumn: [
        {
          type: "checkbox",
          width: 44,
          align: 'center'
        },
        {
          title: '操作',
          fixed: "right",
          width: 80,
          slots: {
            default: (scope) => {
              return [
                <el-button size="small" type="text" disabled={this.authority.b_info !== 1} onClick={() => this.handleClick(scope)} > 查看 </el-button>,
                <el-button size="small" type="text" disabled={!(scope.row.auditor_status == 1 && this.authority.b_edit == 1) || scope.row.bill_source == 2} onClick={() => this.updateClick(scope.row)}  > 修改</el-button>
              ]
            }
          }
        },
        {
          title: '财务组织',
          field: 'company_name',
          width: 130
        },
        {
          title: '单据号',
          field: 'payment_sn',
          width: 180,
          slots: {
            default: (scope) => {
              return [
                <el-link disabled={this.authority.b_info !== 1} onClick={() => { this.handleClick(scope) }} type="primary">
                  {scope.row.payment_sn} </el-link>
              ]
            }
          }
        },
        {
          title: '供应商(厂家)',
          field: 'supplier_name',
          width: 150
        },
        {
          title: '物流公司',
          field: 'logistics_name',
          width: 200,
          visible: !Boolean(Number(localStorage.getItem("is_company_service")))
        },
        {
          title: '往来类型',
          field: 'factory_accounts_type_name',
          width: 180
        },
        {
          title: '付款金额',
          field: 'total_price',
          headerAlign: "right", // 表头对齐方式
          align: "right", // 列对齐方式
          formatter: 'priceFormatNone',
          width: 130
        },
        {
          title: '部门',
          field: 'department_name',
          width: 100
        },
        {
          field: 'employee_name',
          title: '业务员',
          width: 100
        },
        {
          title: '创建人',
          field: 'add_user_name',
          width: 120
        },
        {
          title: '业务日期',
          field: 'business_time',
          width: 100
        },
        {
          title: '单据时间',
          field: 'add_time',
          width: 180
        },
        {
          title: '供应商确认收款',
          field: 'confirm_receipt',
          visible: Boolean(Number(localStorage.getItem("is_company_service"))),
          width: 180
        },
        {
          title: '审核状态',
          field: 'auditor_status_name',
          width: 80
        },
        {
          title: '审核人',
          field: 'auditor_name',
          width: 130
        },
        {
          title: '审核时间',
          field: 'auditor_time',
          width: 180
        },
        {
          title: '修改人',
          field: 'update_user_name',
          width: 130
        },
        {
          title: '修改时间',
          field: 'update_time',
          width: 130
        },
        {
          title: '备注',
          field: 'remark',
          width: 150
        }
      ]
    }
  },
  methods: {
    footerMethod({ columns, data }) {
      return [
        columns.map((column, columnIndex) => {
          if (columnIndex === 2) {
            return "页小计";
          }
          if (["total_price"].includes(column.property)) {
            var num = this.$utils.sum(data, column.property);
            if (column.property === "total_price") {
              return num
                .toFixed(2)
                .toString()
                .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1" + ",");
            }
            if (column.property === "num") {
              return num
                .toString()
                .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1" + ",");
            }
            return Number.isInteger(num) ? num : num.toFixed(2);
          }
          return null;
        })
        ,
        columns.map((column, columnIndex) => {
          if (columnIndex === 2) {
            return "合计";
          }
          if (column.property === 'total_price' && this.footerData.payment_total_price) {
            return (this.footerData.payment_total_price - 0)
              .toFixed(2)
              .toString()
              .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1" + ",");
          }
          if (["total_price"].includes(column.property)) {
            return '加载中...'
          }
          return null;
        })]
    }
  }
}